@font-face {
    font-family: 'dohyeon';
    src: url('../BMDOHYEON_ttf.ttf');
}

* {
    box-sizing: border-box;
    font-family:'dohyeon';
}

body {
    margin: 0;
    padding: 0;
    font-family:'dohyeon' ;
    background-image: linear-gradient(to right top, /*오른쪽으로 그라데이션*/
    #f9f871,
    #ffe469,
    #ffd166,
    #ffbe66,
    #ffac69,
    #ff9d6d,
    #ff8f73,
    #ff827b,
    #fc7486,
    #f46993,
    #e861a2,
    #d65db1);
    height: 100%;
}