.bg {
    position: fixed; /*요소 위치 고정*/
    top: 0;
    left: 0;
    /*고정종횡비 (가로 대 세로 비율)*/
    min-width: 100%;
    min-height: 100%;
    filter: brightness(50%); /*이미지 밝기 조절(100초과 밝게 100미만 어둡게)*/
}
.show {
    position: absolute; /*부모 요소 위치 기준으로 배치됨*/
    top: 20%;
    left: 20%;
    z-index: 8;

    display: flex;
    align-items: center;
}
.img {
    height: 345px;
}
.textbox {
    padding: 0 16px 0 32px; /*위 오른쪽 아래 왼쪽*/
    width: 400px;
    height: 345px;
}
.title {
    font-weight: bold;
    font-size: 28px;
}
.title:hover {
    text-decoration: underline;
}
.title a {
    color: white;
    text-decoration: none;
}
.textbox ul {
    padding: 0 0 0 20px;
    color: white;
    font-size: 18px;
}
.textbox p {
    color: whitesmoke;
    font-weight: 300;
    font-size: 14px;
}