.container {
    position: relative;
}
.slide_show {
    width: 1050px; /*슬라이드 3개만 보이도록*/
    overflow: hidden; /*슬라이드 넘어가는 내용 안 보이도록*/

    position: relative;
    left: calc(50% - 525px);
}
.slide {
    width: 3500px; /*슬라이드 전체 길이 (10개) */
    padding: 50px 0 0 0;
    display: flex;

    transition: all 0.4s ease;
}
.left,
.right {
    background-color: transparent;
    border: none;
    outline: none;
    color: gray;
    opacity: 0.8;
    font-size: 44px;
    transition: all 0.4s;
}
.left:hover,
.right:hover {
    cursor: pointer;
    transform: scale(1.1);
}
.left {
    text-shadow: 3px 2px 0 darkgray;

    position: absolute;
    top: calc(50% - 22px);
    left: 0px;
}
.right {
    text-shadow: -3px 2px 0 darkgray;

    position: absolute;
    top: calc(50% - 22px);
    right: 0px;
}