:root { /*css 내에서 사용할 변수 만들기*/
    --green-color-3 : #41227f;
    --green-color-2 : #dcb0ff;
    --green-color-1 : #faccff;
}
.container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 4px 64px;
    background-color: transparent;
    position: fixed;
    left: 0;
    right: 0;
    z-index: 10;

    transition: 0.4s ease;
}
.container a {
    text-decoration: none;
}
.container ul {
    list-style: none;
    padding: 0;
}
.title {
    display: flex;
    align-items: center;
}
.title i {
    font-size: 30px;
    color: var(--green-color-1);
    margin: 0 8px 0 0;
}
.title strong {
    font-size: 30px;
    color: var(--green-color-1);
    letter-spacing: 0.5px; /*글자사이 간격*/
}
.option_list {
    display: flex;
}
.option_list li a {
    font-size: 14px;
    padding: 4px 12px;
    color: var(--green-color-1);
}
.option_list li a:hover {
    cursor: pointer; /*커서 모양 손가락으로 변경*/
    color: var(--green-color-2);
}
.icon_list {
    display: flex;
}
.icon_list li {
    font-size: 24px;
    padding: 4px 8px;
}
.icon_list li a{
    color: var(--green-color-1);
}
.null {
    width: 100%;
    height: 100px; /*네비 바와 콘텐츠 사이 여백 담당*/
}

@media screen and (max-width : 1090px) {
    .option_list {
        display: none;
    }
}